/** @jsx jsx */
import { jsx } from '@emotion/core';
import styled from '@emotion/styled';
import { Fragment, useMemo } from 'react';
import { Member, Social } from 'src/models';
import Card from 'src/components/Card';
import { Color, SocialIcons } from 'src/constants';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

interface Props {
  member: Member;
}
const MemberCard = ({ member }: Props) => {
  const socials: Array<[string, string]> = useMemo(() => Object.entries(member.social), [member.social]);
  return (
    <AnimatedListItem>
      <Card>
        <Card.Img src={member.profileImg} />
        <Card.Body>
          <Card.Title>
            {member.name.ko} / {member.name.en}
          </Card.Title>
          <Card.Subtitle>
            {member.role}
            {member.company ? ` / ${member.company}` : ''}
          </Card.Subtitle>
          <Links>
            <Fragment>
              {socials.map(([key, socialUrl]) => (
                <LinkIcon key={key}>
                  <a href={socialUrl} target="_blank" rel="noopener noreferrer">
                    <i className={`${SocialIcons[key as keyof Social]}`} />
                  </a>
                </LinkIcon>
              ))}
            </Fragment>
          </Links>
        </Card.Body>
      </Card>
    </AnimatedListItem>
  );
};

const Links = styled.ul`
  margin: 1rem 0 0 0;
  padding: 0;
`;
const LinkIcon = styled.li`
  list-style: none;
  display: inline-block;
  margin-right: 1rem;
  margin-bottom: 0;
  &:last-child {
    margin-right: 0;
  }
  i {
    color: ${Color.Grey600};
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
    &:hover {
      transform: translate(0, -5px);
      opacity: 0.8;
    }
  }
`;

export default MemberCard;
