import { FluidObject, FixedObject } from 'gatsby-image';

export enum MemberId {
  Evan = 'evan',
  Daniel = 'daniel',
  Simon = 'simon',
  MartinH = 'martin_h',
  MartinK = 'martin_k',
  Jim = 'jim',
  Ryan = 'ryan',
}

export interface Social {
  github: string;
  blog: string;
  linkedin: string;
  unsplash: string;
}

export interface Member {
  id: MemberId;
  name: {
    ko: string;
    en: string;
  };
  role: string;
  profileImg: string;
  company: string;
  social: Partial<Social>;
  rss?: string;
}

export interface Menu {
  name: string;
  link: string;
  externalLink: boolean;
}

export interface BlogFeed {
  author: Member;
  title: string;
  contentSnippet: string;
  pubDate: string;
  link: string;
}

interface ImageSharp {
  childImageSharp: {
    fixed?: FixedObject;
    fluid?: FluidObject;
  };
}

interface Frontmatter {
  date: string;
  path: string;
  title: string;
  thumbnail?: ImageSharp;
}
export interface Magazine {
  id: number;
  frontmatter: Frontmatter;
}

export interface MagazineContents {
  body: string;
  frontmatter: Frontmatter;
}

export interface MainContent {
  title: string;
  description: string;
  menu: Menu;
  buttonTitle: string;
  photo: string;
}
